import React, { useState } from 'react';
import { questions } from './SurveyQuestions';
import { Stack, Divider, Typography, Radio, RadioGroup, FormControlLabel, Button } from '@mui/material';
import { Formik } from 'formik';
import IntlMessages from '@jumbo/utils/IntlMessages';
import ChevronRightRoundedIcon from '@mui/icons-material/ChevronRightRounded';
export default function CallSurvey({ callId }) {
  const [startSurvey, setStartSurvey] = useState(false);
  const questionsArray = questions.map((item, index) => ({
    title: item.title,
    question: item.question,
    point: item.point,
    answer: '',
  }));
  const questionsObjFormik = questionsArray.reduce((a, value) => ({ ...a, [value.question]: '' }), {});
  return (
    <div style={{ marginTop: '1rem', textAlign: 'center' }}>
      {!startSurvey ? (
        <Button
          fullWidth
          variant="contained"
          color="info"
          onClick={() => setStartSurvey(true)}
          endIcon={<ChevronRightRoundedIcon />}>
          <IntlMessages id={'startSurvey'} />
        </Button>
      ) : (
        <>
          <Typography variant="h4" component="div">
            <IntlMessages id={'survey'} />
          </Typography>
          <Formik
            initialValues={questionsObjFormik}
            onSubmit={values => {
              let score = 0;
              let answers = Object.values(values);
              for (let i = 0; i < questionsArray.length; i++) {
                questionsArray[i].answer = answers[i];
                if (answers[i] === 'EVET') {
                  if (questionsArray[i].title === 'SurveyTitle13') {
                    score *= questionsArray[i].point;
                  } else {
                    score += questionsArray[i].point;
                  }
                }
              }
              console.log('score', score);
            }}>
            {({ handleSubmit, handleChange }) => (
              <form onSubmit={handleSubmit}>
                <Stack spacing={2} divider={<Divider orientation="horizontal" flexItem />}>
                  {questionsArray.map((item, index) => (
                    <div key={index}>
                      <Typography variant="h5" component="div">
                        <IntlMessages id={item.title} />
                      </Typography>
                      <Typography variant="h6" component="div">
                        <b>{`${index + 1}) `}</b>
                        <span>
                          <IntlMessages id={item.question} />
                        </span>
                      </Typography>
                      <RadioGroup style={{ justifyContent: 'center' }} row name={item.question} onChange={handleChange}>
                        <FormControlLabel value="EVET" control={<Radio />} label={<IntlMessages id={'yes'} />} />
                        <FormControlLabel value="HAYIR" control={<Radio />} label={<IntlMessages id={'no'} />} />
                      </RadioGroup>
                    </div>
                  ))}
                </Stack>
                <Button fullWidth type="submit" variant="contained" color="secondary">
                  <IntlMessages id={'send'} />
                </Button>
              </form>
            )}
          </Formik>
        </>
      )}
    </div>
  );
}
